'use client';

import { graphql, useLazyLoadQuery } from 'react-relay/hooks'

const HomePageQuery = graphql`query homepageQuery($lat: Float!, $lon: Float!, $method: Int, $school: Int, $date: String) {
        getPrayerTimes(lat: $lat, lon: $lon, method: $method, school: $school, date: $date) {
            fajr
            dhuhr
            asr
            maghrib
            isha
            sunrise
        }
    }
`;

export default function HomePage() {
    const data = useLazyLoadQuery(HomePageQuery, {
        lat: 39.0987189,
        lon: -77.5703059,
        method: 2,
        school: 0,
    });
    const prayerTimes = data.getPrayerTimes;
    return (
        <div style={{
            minHeight: '100vh',
            padding: '2rem',
            fontFamily: 'system-ui, -apple-system, BlinkMacSystemFont, sans-serif',
            background: '#0f172a',
            color: 'white',
        }}>
            <h1 style={{ fontSize: '2rem', marginBottom: '1rem' }}>
                ðŸ•Œ Prayer Times Dashboard
            </h1>
            <ul>
                <li>Fajr: {prayerTimes.fajr}</li>
                <li>Dhuhr: {prayerTimes.dhuhr}</li>
                <li>Asr: {prayerTimes.asr}</li>
                <li>Maghrib: {prayerTimes.maghrib}</li>
                <li>Isha: {prayerTimes.isha}</li>
                <li>Sunrise: {prayerTimes.sunrise}</li>
            </ul>
        </div>
    );
}


